																	<button data-v-de2bfc3e="" type="button"
																			onClick={this.onClickChangeAvatar(false)}
																			aria-label="Close" className="btnclose">
																		<img alt=''
																			data-v-de2bfc3e=""
																			src="data:image/svg+xml;base64," />
																	</button>
